import React, { useEffect } from "react";
import logo from "../../styles/Images/shield-logo/logo-transparent-png.png";
import { bottomAnim } from "../core/utils";

const TellAbout = () => {
  useEffect(() => {
    bottomAnim();
  }, []);

  return (
    <section className="section-cta" id="talkus">
      <div className="container">
        <div className="row tell_about-section">
          <div className="col-md-8 tell_about-content">
            <div className="cta-text-box">
              <h1 className="bottomAnim ">Tell Us About Your Project</h1>
              <p className="bottomAnim cta-text">
                Let Us Help To Your Business Process Outsourcing and Business
                Solutions.
              </p>
              <form className="cta-form" action="#">
                <div className="row">
                  <div className="col-md-6">
                    <div className="bottomAnim mb-4">
                      <label for="full-name">Full Name</label>
                      <input
                        id="full-name"
                        type="text"
                        placeholder="John Doe"
                        required
                      />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="bottomAnim mb-4">
                      <label for="email">Email address</label>
                      <input
                        id="email"
                        type="email"
                        placeholder="me@example.com"
                        required
                      />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="bottomAnim mb-4">
                      <label for="select-where">
                        Where did you hear from us?
                      </label>
                      <select id="select-where" required>
                        <option value="">Please choose one option:</option>
                        <option value="friends">Friends and family</option>
                        <option value="youtube">YouTube video</option>
                        <option value="podcast">Podcast</option>
                        <option value="ad">Facebook ad</option>
                        <option value="others">Others</option>
                      </select>
                    </div>
                  </div>
                </div>
                <button className="bottomAnim btn btn--form d-flex align-items-center mt-3">
                  Sign up now
                </button>
              </form>
            </div>
          </div>
          <div className="col-md-4 tell_about-img">
            <div className="cta-img-box">
              <img className="form-img" src={logo} alt="tell-about-img" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default TellAbout;
