import React from "react";
import logo from "../../styles/Images/shield-logo/logo-transparent-svg.svg";
import whatsapp from "../../styles/Images/socialicons/whatsapp.png";
import email from "../../styles/Images/socialicons/email.png";

const Footer = () => {
  return (
    <footer>
      <div className="container">
        <div className="row">
          <div className="col-sm-12 col-md-6 col-lg-3 d-flex align-items-center">
            <img className="footer-img w-50" src={logo} alt="logo-img" />
          </div>
          <div className="col-sm-12 col-md-6 col-lg-3 d-flex align-items-center">
            <div className="d-block">
              <h4>Company</h4>
              <p>About Shield Infotech</p>
              <p>For Business</p>
              <p>Client Partners</p>
            </div>
          </div>
          <div className="col-sm-12 col-md-6 col-lg-3  d-flex align-items-center">
            <div className="d-flex justify-content-center">
              <div className="d-block">
                <h4>Contact</h4>
                <p>40/41, Palmol cross street Madurai 625009</p>
                <a href="mailto:shieldinfotechmdu@gmail.com">
                  <p>shieldinfotechmdu@gmail.com</p>
                </a>
                <a href="https://wa.me/7010734488">
                  <p>+91 7010734488</p>
                </a>
              </div>
            </div>
          </div>
          <div className="col-sm-12 col-md-6 col-lg-3 d-flex align-items-center">
            <div className="d-flex justify-content-center">
              <div className="d-block">
                <h4>Connect US</h4>
                <div className="connect">
                  <a href="https://wa.me/7010734488">
                    <img
                      className="social-icons"
                      src={whatsapp}
                      alt="whatsapp-icon"
                    />
                    <p>Whatsapp</p>
                  </a>
                </div>
                <div className="connect">
                  <a href="mailto:shieldinfotechmdu@gmail.com">
                    <img
                      className="social-icons"
                      src={email}
                      alt="email-icon"
                    />
                    <p>Email</p>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
